<template>
  <div>
    <v-header>
    </v-header>

    <v-content :type="'none'" style="background-color:#fff;">
      <div class="card-box">
        <div :class="['card-bg',front.length > 0 ? 'active':'']">
          <span class="img card tran-all-3" v-show="front.length > 0" style="background-image:url({{front}})"></span>
          <input type="file" accept="image/jpeg,image/png,image/jpg,image/gif" id="front" class="upload opa-hide z-index-2">
          <span class="img add"></span>
          <p class="color-9 tip">身份证正面照片</p>
        </div>
        <div :class="['card-bg',back.length > 0 ? 'active':'']">
          <span class="img card tran-all-3" v-show="back.length > 0" style="background-image:url({{back}})"></span>
          <input type="file" accept="image/jpeg,image/png,image/jpg,image/gif" id="back" class="upload opa-hide z-index-2">
          <span class="img add"></span>
          <p class="color-9 tip">身份证反面照片</p>
        </div>
      </div>
      <v-inner-btn>
        <input class="input tip" value="照片示例" type="button" @click="getTip"/>
      </v-inner-btn>
      <v-inner-btn style="margin-top:.15rem">
        <input :class="['input',front && back ? 'ok' : 'nk']" value="提交审核" type="button" @click="doVerify"/>
      </v-inner-btn>
    </v-content>
    <div :class="['pic-tip','tran-all-3','z-index-max',showTip ? 'active' : '']">
      <div class="warp">
        <span class="img close" @click="closeTip"></span>
        <span class="img pic-front"></span>
        <p style="padding-top: 1.65rem;">正面照示例</p>
        <span class="img pic-back"></span>
        <p style="padding-top: 1.3rem;">反面照示例</p>
      </div>
    </div>
  </div>
</template>
<script>
  import {url, lt} from '../../../const'
  import {toast} from '../../../assets/js/toast'
  import VHeader from '../../../components/Header'
  import VContent from '../../../components/Content'
  import VInnerBtn from '../../../components/InnerBtn'
  require('../../../assets/js/lrz.all.bundle');
  import $ from 'jquery'

  export default{
    route: {
      data({to:{query}}){
        this.front = '';
        this.back = '';
        this.type = query.type;
        this.showTip = false;
        return this.$http.get(url.autonymVerify).then(
                ({data:{data}}) => {
                  if (data.realStatus == 2) {
                    toast.alert('认证审核中');
                    window.location.href = "#/identify/real/step1";
                  }
                  if (data.realStatus == 4) {
                      toast.alert('您已实名认证');
                      window.location.href = "#/identify/real/step1";
                  }
                  let userInfo = data.userInfo;
                  if (!userInfo.realName) {
                    toast.alert('请先实名认证后再上传');
                    window.location.href = "#/identify/real/step1";
                  }
                })
      }
    },
    ready(){
      let vm = this;
      let scale = $(window).width() / 320;
      scale = scale < 1 ? 1 : scale;
      $('.card-box .card-bg').width($('.card-box .card-bg').width() * scale);
      $('.card-box .card-bg').height($('.card-box .card-bg').height() * scale);
      $(".upload").click(function () {
        $(this).val('');
      });
      $(".upload").change(function () {
        if (!this.files || !this.files.length) return;
        var files = this.files[0];
        var obj = this;
        if (!/image\/\w+/.test(files.type)) {
          toast.alert("图片格式不正确，请选择正确格式的图片文件！");
          return false;
        } else {
          var fileReader = new FileReader();
          fileReader.onload = function (e) {
            let ua = navigator.userAgent.toLowerCase();
            if (/android/.test(ua)) {
              toast.loadEnd();
              var base64 = this.result;
              if ($(obj).is('#front')) {
                vm.front = base64;
                vm.frontFile = files;
              } else {
                vm.back = base64;
                vm.backFile = files;
              }
            } else {
              lrz(files, {width: 667})
                      .then(function (rst) {
                        toast.loadEnd();
                        var base64 = rst.base64;
                        if ($(obj).is('#front')) {
                          vm.front = base64;
                        } else {
                          vm.back = base64;
                        }
                      })
                      .catch(function (err) {
                        toast.alert("图片处理失败");
                      });
            }
          };
          fileReader.onerror = function (e) {
            toast.alert("图片加载失败");
          };
          fileReader.readAsDataURL(files);
          toast.loading();
        }
      });
    },
    data(){
      return {
        front: '',
        frontFile: {},
        back: '',
        backFile: {},
        showTip: false,
        // realName:'',
        // identifyNumber:'',
        cuted: 0,//已裁剪数量
        type: 0
      }
    },
    methods: {
      getTip(){
        this.showTip = true;
        toast.mask(this.closeTip, true);
      },
      closeTip(){
        this.showTip = false;
        toast.hide_mask();
      },
      doVerify(){
        if (this.front && this.back) {
          toast.loading();
          let vm = this;
          vm.cuted = 0;
          let ua = navigator.userAgent.toLowerCase();
          if (/android/.test(ua)) {
            setTimeout(function () {
              lrz(vm.frontFile, {width: 667})
                      .then(function (rst) {
                        vm.front = rst.base64;
                        vm.cuted = vm.cuted + 1;
                      });
              lrz(vm.backFile, {width: 667})
                      .then(function (rst) {
                        vm.back = rst.base64;
                        vm.cuted = vm.cuted + 1;
                      });
            }, 100);
          } else {
            this.$nextTick(function () {
              vm.cuted = 2;
            });
          }
        }
      }
    },
    watch: {
      'cuted': function () {
        if (this.cuted == 2) {
          this.$http.get(url.verifyPhoto, {
            front: this.front,
            back: this.back
          }).then(
                  ({data:{data, success, resultMsg}}) => {
                    if (success) {
                      // if(data.verified){
                      //   toast.alert('您已认证');
                      // }else{
                      toast.alert('提交成功，等待审核');
                      // }
                      if (this.type == 1) {
                        window.location.href = "#/wallet/recharge";
                      } else if (this.type == 2) {
                        window.location.href = "#/wallet/cash";
                      } else if (this.type == 3) {
                        window.location.href = "#/wallet/bankCard";
                      } else {
                        window.history.back();
                      }
                    } else {
                      toast.alert(resultMsg);
                    }
                  })
        }
      }
    },
    components: {VHeader, VContent, VInnerBtn}
  }
</script>
<style scoped>
  .card-box {
    margin-top: .25rem;
  }

  .card-bg {
    margin: 0 auto .2rem;
    width: 1.85rem;
    height: 1.2rem;
    position: relative;
    overflow: hidden;
    border: .01rem dashed #e5e5e5;
    border-radius: .03rem;
  }

  .card-bg.active {
    border: none;
  }

  .card-bg .add, .card-bg .tip {
    z-index: 1;
  }

  .card-bg .card {
    z-index: 10;
  }

  .card-bg .upload {
    z-index: 100;
  }

  .img.add {
    position: absolute;
    top: 30%;
    left: 50%;
    margin-left: -.125rem;
    width: .25rem;
    height: .25rem;
    background-size: .25rem;
    background-image: url(../../../assets/img/icon/add_i01.png);
  }

  .card-bg .tip {
    margin-top: 35%;
    text-align: center;
  }

  .img.card, .card-bg .upload {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .img.card {
    background-size: 100% 100%;
  }

  .pic-tip {
    position: fixed;
    height: 3.5rem;
    width: 2.6rem;
    top: 100%;
    left: 50%;
    margin-left: -1.3rem;
  }

  .pic-tip.active {
    top: 15%;
  }

  .pic-tip .warp {
    height: 100%;
    width: 100%;
    background-color: #fff;
    position: relative;
    text-align: center;
    overflow: hidden;
    border-radius: .1rem;
  }

  .img.pic-front, .img.pic-back {
    position: absolute;
    width: 1.85rem;
    height: 1.2rem;
    left: 50%;
    margin-left: -.925rem;
    background-size: 100% 100%;
  }

  .img.close {
    width: .1rem;
    height: .1rem;
    background-size: .1rem;
    position: absolute;
    right: .15rem;
    top: .15rem;
    background-image: url(../../../assets/img/icon/closeBtn03.png);
  }

  .img.pic-front {
    top: .4rem;
    background-image: url(../../../assets/img/bg/front.jpg);
  }

  .img.pic-back {
    top: 1.9rem;
    background-image: url(../../../assets/img/bg/back.jpg);
  }
</style>



